.Hover {
  &-bg {
    &1 {
      margin-top: -15vh;
      padding: 0;
      width: 100vw;
      height: 100vh;
      position: absolute;
      z-index: 5;
      background-color: #323638;
      text-align: justify;

      &.a {
        opacity: 0;
        animation: hoverAppear1 1s ease 0s;
        animation-fill-mode: forwards;

        @keyframes hoverAppear1 {
          0% {
            opacity: 0;
          }

          100% {
            opacity: 1;
          }
        }
      }
    }

    &2 {
      margin-top: -15vh;
      padding: 0;
      width: 100vw;
      height: 100vh;
      position: absolute;
      z-index: 5;
      background-color: #2975b1;

      &.a {
        opacity: 0;
        animation: hoverAppear2 1s ease 0s;
        animation-fill-mode: forwards;

        @keyframes hoverAppear2 {
          0% {
            opacity: 0;
          }

          100% {
            opacity: 1;
          }
        }
      }
    }

    &3 {
      margin-top: -15vh;
      padding: 0;
      width: 100vw;
      height: 100vh;
      position: absolute;
      z-index: 5;
      background-color: #efe8f0;

      &.a {
        opacity: 0;
        animation: hoverAppear3 1s ease 0s;
        animation-fill-mode: forwards;

        @keyframes hoverAppear3 {
          0% {
            opacity: 0;
          }

          100% {
            opacity: 1;
          }
        }
      }
    }

    &:after {
      content: "";
      display: inline-block;
      width: 100%;
    }
  }



  &-backText {
    color: white;
    width: 100%;
    position: absolute;

    &.back1 {
      opacity: 0.03;
      margin: -1.5vw;

      font: {
        family: proxima nova, sans-serif;
        weight: 700;
        size: 43vw;
      }
    }

    &.back2 {
      opacity: 0.1;
      margin: -2.5vw;
      margin-top: 8vh;

      font: {
        family: proxima nova, sans-serif;
        weight: 700;
        size: 33vw;
      }
    }

    &.back3 {
      opacity: 0.52;
      margin: -2.5vw;
      margin-top: 12vh;

      font: {
        family: proxima nova, sans-serif;
        weight: 700;
        size: 29vw;
      }
    }
  }

  &-basicLogo {
    width: 70vw;
    opacity: 0;
    position: absolute;
    top: 30vh;
    left: 15vw;
    transition: 1s;
    z-index: 2;

    animation: hoverLogo 1s ease 0s;
    animation-fill-mode: forwards;

    @keyframes hoverLogo {
      0% {
        opacity: 0;
      }

      100% {
        opacity: 1;
      }
    }

    &2 {
      width: 70vw;
      opacity: 1;
      position: absolute;
      top: 30vh;
      left: 15vw;
    }
  }

  /*START HANDLE PROGRAMMING BACK ITEMS*/
  &-line1 {
    width: 25vw;
    position: absolute;
    left: 9.4vw;
    top: calc(30vh + 8.7vw);

    clip-path: inset(0vh 40vw 0vh 0vw);
    animation: line1 2s ease 0s;
    animation-fill-mode: forwards;

    @keyframes line1 {
      0% {
        clip-path: inset(0vh 40vw 0vh 0vw);
      }

      100% {
        clip-path: inset(0vh 0vw 0vh 0vw);
      }
    }

    &d {
      width: 25vw;
      position: absolute;
      left: 9.4vw;
      top: calc(30vh + 8.7vw);

      clip-path: inset(0vh 0vw 0vh 0vw);
      animation: line1d 1.5s ease 0s;
      animation-fill-mode: forwards;

      @keyframes line1d {
        0% {
          clip-path: inset(0vh 0vw 0vh 0vw);
        }

        100% {
          clip-path: inset(0vh 0vw 0vh 40vw);
        }
      }
    }
  }

  &-line2 {
    width: 15vw;
    position: absolute;
    top: calc(30vh + 13vw);
    left: 50vw;

    clip-path: inset(0vh 40vw 0vh 0vw);
    animation: line2 2s ease 0.5s;
    animation-fill-mode: forwards;

    @keyframes line2 {
      0% {
        clip-path: inset(0vh 40vw 0vh 0vw);
      }

      100% {
        clip-path: inset(0vh 0vw 0vh 0vw);
      }
    }

    &d {
      opacity: 0;
    }
  }

  &-line3 {
    width: 32vw;
    position: absolute;
    left: 26.8vw;
    top: calc(30vh + 12.5vw);

    clip-path: inset(0vh 40vw 0vh 0vw);
    animation: line3d 2s ease 0.5s;
    animation-fill-mode: forwards;

    @keyframes line3d {
      0% {
        clip-path: inset(0vh 40vw 0vh 0vw);
      }

      100% {
        clip-path: inset(0vh 0vw 0vh 0vw);
      }
    }

    &d {
      width: 32vw;
      position: absolute;
      left: 26.8vw;
      top: calc(30vh + 12.5vw);

      clip-path: inset(0vh 0vw 0vh 0vw);
      animation: line3 1.5s ease 0s;
      animation-fill-mode: forwards;

      @keyframes line3 {
        0% {
          clip-path: inset(0vh 0vw 0vh 0vw);
        }

        100% {
          clip-path: inset(0vh 0vw 0vh 40vw);
        }
      }
    }
  }

  &-line4 {
    width: 23vw;
    position: absolute;
    left: 53.7vw;
    top: calc(30vh + 18.5vw);

    clip-path: inset(0vh 40vw 0vh 0vw);
    animation: line4 2s ease 1s;
    animation-fill-mode: forwards;

    @keyframes line4 {
      0% {
        clip-path: inset(0vh 40vw 0vh 0vw);
      }

      100% {
        clip-path: inset(0vh 0vw 0vh 0vw);
      }
    }

    &d {
      width: 23vw;
      position: absolute;
      left: 53.7vw;
      top: calc(30vh + 18.5vw);

      clip-path: inset(0vh 0vw 0vh 0vw);
      animation: line4d 1.5s ease 0s;
      animation-fill-mode: forwards;

      @keyframes line4d {
        0% {
          clip-path: inset(0vh 0vw 0vh 0vw);
        }

        100% {
          clip-path: inset(0vh 0vw 0vh 40vw);
        }
      }
    }
  }

  /*START HANDLE DESIGN BACK ITEMS*/
  &-design1 {
    width: 55vw;
    position: absolute;
    left: 39vw;
    top: 10vh;

    clip-path: inset(0vh 55vw 0vh 0vw);
    animation: design1 1.5s ease 0s;
    animation-fill-mode: forwards;

    @keyframes design1 {
      0% {
        clip-path: inset(0vh 55vw 0vh 0vw);
      }

      100% {
        clip-path: inset(0vh 0vw 0vh 0vw);
      }
    }
  }

